package firebase

// A wrapper class to manage Firebase instances and provide Kotlin-friendly APIs
class FirebaseWrapper {
    private val firebase: dynamic
    private val analytics: dynamic

    init {
        // Initialize Firebase with your config
        val config = jsObject<FirebaseConfig> {
            apiKey = "AIzaSyBCAEf4Rcn_WZLb9L0p_b4CicpO9PWaRlI"
            authDomain = "integrated-gaming-leagues.firebaseapp.com"
            projectId = "integrated-gaming-leagues"
            storageBucket = "integrated-gaming-leagues.firebasestorage.app"
            messagingSenderId = "50304558274"
            appId = "1:50304558274:web:4a3bdad0cfb2b6893d475b"
            measurementId = "G-FX585KK7F3"
        }

        if (Firebase.apps.isEmpty()) {
            println("Initializing Firebase...")
            firebase = Firebase.initializeApp(config)
        } else {
            println("Firebase already initialized. ${Firebase.apps}")
            firebase = Firebase
        }
        analytics = FirebaseAnalytics.getAnalytics()
    }

    // Analytics operations
    fun logEvent(eventName: String, params: dynamic = null) {
        FirebaseAnalytics.logEvent(analytics, eventName, params)
    }

    fun logScreenView(screenName: String) {
        logEvent("screen_view", object {
            val screen_name = screenName
        }.asDynamic())
    }

    fun logUserAction(actionName: String, actionCategory: String) {
        logEvent("user_action", object {
            val action = actionName
            val category = actionCategory
        }.asDynamic())
    }
}