package firebase

@JsName("Object")
external class Object {
    companion object {
        fun assign(dest: dynamic, vararg src: dynamic): dynamic
    }
}

external interface FirebaseConfig {
    var apiKey: String
    var authDomain: String
    var projectId: String
    var storageBucket: String
    var messagingSenderId: String
    var appId: String
    var measurementId: String
}

// Helper function to create JS objects
fun <T> jsObject(builder: T.() -> Unit): T {
    val obj = js("{}").unsafeCast<T>()
    builder(obj)
    return obj
}

@JsModule("firebase/app")
@JsNonModule
external object Firebase {
    fun initializeApp(config: FirebaseConfig): dynamic
    val apps: Array<dynamic>
}

@JsModule("firebase/analytics") 
@JsNonModule
external object FirebaseAnalytics {
    fun getAnalytics(): dynamic
    fun logEvent(analytics: dynamic, eventName: String, eventParams: dynamic = definedExternally)
}


